                  </div>
                </div>
                <div v-if="d.status == 'Available'" class="col-span-1 flex justify-end items-end py-2">
                  <button v-if="!d.cart" @click="AddToCart(d)" :disabled="d.status != 'Available'" class="bg-cusYellow mr-5 disabled:bg-gray-500 disabled:hover:text-white disabled:hover:scale-100 disabled:cursor-not-allowed w-36 py-1 rounded-2xl text-gray-100 hover:bg-yellow-400 hover:text-black hover:scale-110">Add to Cart</button>
