<script>
import NavbarNotLoggedIn from '@/components/NavbarNotLoggedIn.vue';
import PriceRangeSlider from '@/components/PriceRangeSlider.vue';
import api from '../../boot/axios.js';
export default {
  components: { NavbarNotLoggedIn, PriceRangeSlider },
  data() {
    return {
      dataTag: [],
      all: true,
      available: false,

      ascending: true,
      descending: false,
      filter: false,

      minValue: -1,
      maxValue: -1,

      showModal: false,
      modalD: [],
    };
  },
  created() {
    this.getData();
  },

  methods: {
    Modal(d) {
      this.modalD = d;
      console.table(d);
      this.showModal = true;
    },
    handleRangeUpdate(data) {
      // Update the data in the parent component
      this.minValue = data.minValue;
      this.maxValue = data.maxValue;

      console.log(this.minValue, this.maxValue);
      this.getData();
    },
    async getData() {
      let a = this.$store.getters.cartItems;
      await api
        .post(`api/tag_view/`, {
          status: this.available,
          min_price: this.minValue,
          max_price: this.maxValue,
          sort: this.ascending,
        })
        .then((res) => {
          this.dataTag = res.data;
          for (let i = 0; i < this.dataTag.length; i++) {
            for (let j = 0; j < a.length; j++) {
              if (this.dataTag[i].id == a[j].id) {
                console.log(true);
                this.dataTag[i].cart = true;
                break;
              } else {
                console.log(false);
                this.dataTag[i].cart = false;
              }
            }
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    AddToCart(item) {
      item.cart = true;
      let a = {
        id: item.id,
        tag: item.tag,
        weight: item.weight,
        sellingPrice: item.sellingPrice,
        pic: item.pic,
      };
      this.$store.commit('addToCart', a);
    },
    RemoveFromCart(item) {
      item.cart = false;
      this.$store.commit('removeFromCart', item.id);
    },
  },
};
</script>

